.App {
  height: 100vh;
  padding-top: 10px;
  background-color: #0a0a23;
  font-family: sans-serif;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.freecodecamp-logo-contenedor {
  width: 100%;
  margin: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.contenedor-calculadora {
  width: 400px;
  height: 600px;
  padding: 14px 14px 22px;
  background-color: #99c9ff;
  border-radius: 14px;
  border: 5px solid #fff;
}

.fila {
  margin: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
}