.boton-contenedor {
  height: 83px;
  display: flex;
  flex: 1 1;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-size: 38px;
  background-color: #1b1b32;
  color: #fff;
  border-radius: 24px;
  border: 2px solid #fff;
  margin: 4px;
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
}

.boton-contenedor:hover {
  background-color: #2196f3;
}

.operador {
  background-color: green;
}

.operador:hover {
  background-color: #ff9800;
}